// src/components/AppBarWithDrawer.tsx
'use client';

import { useState } from 'react';
import { 
  AppBar, 
  Toolbar, 
  IconButton, 
  Button, 
  Box,
  Drawer, 
  List, 
  ListItem, 
  ListItemButton, 
  ListItemIcon, 
  ListItemText, 
  Collapse,
  Divider,
  Tooltip,
  Typography,
  Avatar
} from '@mui/material';
import {
  Menu as MenuIcon,
  ChevronLeft,
  ChevronRight,
  ExpandLess,
  ExpandMore,
  Home as HomeIcon,
  EmojiEvents as CompetitionsIcon,
  Groups as ParticipantsIcon,
  BarChart as ResultsIcon,
  Settings as SettingsIcon,
  Help as HelpIcon,
  History as RoundsIcon,
  AccountCircle as LoginIcon,
  Close as CloseIcon,
  Logout as LogoutIcon
} from '@mui/icons-material';
import { useAuth } from '@/utils/useAuthGuard';
import Link from 'next/link';

export default function AppBarWithDrawer() {
  const [drawerOpen, setDrawerOpen] = useState(true);
  const [expandedSections, setExpandedSections] = useState<Record<string, boolean>>({
    competitions: true,
    results: true
  });

  const toggleDrawer = () => {
    setDrawerOpen(!drawerOpen);
  };

  const toggleSection = (key: string) => {
    setExpandedSections(prev => ({ ...prev, [key]: !prev[key] }));
  };

  const [loginOpen, setLoginOpen] = useState(false);
  const { user, logout } = useAuth();

  return (
    <>
      {/* AppBar con botón de login */}
      <AppBar position="fixed" sx={{ 
        zIndex: (theme) => theme.zIndex.drawer + 1,
        backgroundColor: '#202123'
      }}>
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={toggleDrawer}
            sx={{ mr: 2 }}
          >
            <MenuIcon />
          </IconButton>
          
          <Typography variant="h6" noWrap component="div" sx={{ flexGrow: 1 }}>
            Competencia de Robots Sumo
          </Typography>
          
          {user ? (
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
              <Avatar sx={{ bgcolor: '#1976d2', width: 32, height: 32 }}>
                {user.role.charAt(0).toUpperCase()}
              </Avatar>
              <Typography variant="body2" sx={{ mr: 1 }}>
                {user.role === 'admin' ? 'Administrador' : 
                 user.role === 'juez' ? 'Juez' : 'Visor'}
              </Typography>
              <IconButton color="inherit" onClick={logout} title="Cerrar sesión">
                <LogoutIcon />
              </IconButton>
            </Box>
          ) : (
            <Button 
              color="inherit"
              startIcon={<LoginIcon />}
              onClick={() => setLoginOpen(true)}
            >
              Login
            </Button>
          )}
        </Toolbar>
      </AppBar>
      
      {/* Drawer estilo ChatGPT */}
      <Drawer
        variant="permanent"
        open={drawerOpen}
        sx={{
          width: drawerOpen ? 240 : 56,
          flexShrink: 0,
          [`& .MuiDrawer-paper`]: { 
            width: drawerOpen ? 240 : 56,
            backgroundColor: '#202123',
            color: 'white',
            boxSizing: 'border-box',
            transition: 'width 0.3s ease',
            overflowX: 'hidden',
            zIndex: (theme) => theme.zIndex.appBar - 1
          },
        }}
      >
        <Box sx={{ display: 'flex', justifyContent: 'flex-end', padding: '8px' }}>
          <Tooltip title={drawerOpen ? 'Contraer menú' : 'Expandir menú'} arrow>
            <IconButton onClick={toggleDrawer} sx={{ color: 'white' }}>
              {drawerOpen ? <CloseIcon /> : <ChevronRight />}
            </IconButton>
          </Tooltip>
        </Box>
        <Divider sx={{ borderColor: 'rgba(255, 255, 255, 0.1)' }} />
        
        <List sx={{ overflow: 'hidden', mt: 1 }}>
          {/* Inicio */}
          <ListItem disablePadding>
            <ListItemButton component={Link} href="/" sx={{ minHeight: 48, px: 2.5 }}>
              <ListItemIcon sx={{ minWidth: 0, mr: drawerOpen ? 3 : 'auto', color: 'white' }}>
                <HomeIcon />
              </ListItemIcon>
              {drawerOpen && <ListItemText primary="Inicio" />}
            </ListItemButton>
          </ListItem>

          {/* Bienvenida */}
          <ListItem disablePadding>
            <ListItemButton component={Link} href="/bienvenida" sx={{ minHeight: 48, px: 2.5 }}>
              <ListItemIcon sx={{ minWidth: 0, mr: drawerOpen ? 3 : 'auto', color: 'white' }}>
                <HelpIcon />
              </ListItemIcon>
              {drawerOpen && <ListItemText primary="Bienvenida" />}
            </ListItemButton>
          </ListItem>

          <Divider sx={{ borderColor: 'rgba(255, 255, 255, 0.1)', my: 1 }} />

          {/* Competencias - con desplegable */}
          <ListItem disablePadding>
            <ListItemButton 
              onClick={() => toggleSection('competitions')}
              sx={{ minHeight: 48, px: 2.5 }}
            >
              <ListItemIcon sx={{ minWidth: 0, mr: drawerOpen ? 3 : 'auto', color: 'white' }}>
                <CompetitionsIcon />
              </ListItemIcon>
              {drawerOpen && (
                <>
                  <ListItemText primary="Competencias" />
                  {expandedSections.competitions ? <ExpandLess /> : <ExpandMore />}
                </>
              )}
            </ListItemButton>
          </ListItem>
          
          <Collapse in={expandedSections.competitions && drawerOpen} timeout="auto" unmountOnExit>
            <List component="div" disablePadding>
              <ListItemButton 
                component={Link}
                href="/competencias/rondas" 
                sx={{ pl: 6, minHeight: 48 }}
              >
                <ListItemIcon sx={{ minWidth: 0, mr: 3, color: 'white' }}>
                  <RoundsIcon />
                </ListItemIcon>
                <ListItemText primary="Rondas" />
              </ListItemButton>
              
              <ListItemButton 
                component={Link}
                href="/dashboard/participantes" 
                sx={{ pl: 6, minHeight: 48 }}
              >
                <ListItemIcon sx={{ minWidth: 0, mr: 3, color: 'white' }}>
                  <ParticipantsIcon />
                </ListItemIcon>
                <ListItemText primary="Participantes" />
              </ListItemButton>
            </List>
          </Collapse>

          {/* Resultados - con desplegable */}
          <ListItem disablePadding>
            <ListItemButton 
              onClick={() => toggleSection('results')}
              sx={{ minHeight: 48, px: 2.5 }}
            >
              <ListItemIcon sx={{ minWidth: 0, mr: drawerOpen ? 3 : 'auto', color: 'white' }}>
                <ResultsIcon />
              </ListItemIcon>
              {drawerOpen && (
                <>
                  <ListItemText primary="Resultados" />
                  {expandedSections.results ? <ExpandLess /> : <ExpandMore />}
                </>
              )}
            </ListItemButton>
          </ListItem>
          
          <Collapse in={expandedSections.results && drawerOpen} timeout="auto" unmountOnExit>
            <List component="div" disablePadding>
              <ListItemButton 
                component={Link}
                href="/resultados/clasificacion" 
                sx={{ pl: 6, minHeight: 48 }}
              >
                <ListItemText primary="Clasificación" />
              </ListItemButton>
              
              <ListItemButton 
                component={Link}
                href="/resultados/estadisticas" 
                sx={{ pl: 6, minHeight: 48 }}
              >
                <ListItemText primary="Estadísticas" />
              </ListItemButton>
            </List>
          </Collapse>

          <Divider sx={{ borderColor: 'rgba(255, 255, 255, 0.1)', my: 1 }} />
          
          {/* Configuración */}
          <ListItem disablePadding>
            <ListItemButton component={Link} href="/configuracion" sx={{ minHeight: 48, px: 2.5 }}>
              <ListItemIcon sx={{ minWidth: 0, mr: drawerOpen ? 3 : 'auto', color: 'white' }}>
                <SettingsIcon />
              </ListItemIcon>
              {drawerOpen && <ListItemText primary="Configuración" />}
            </ListItemButton>
          </ListItem>
        </List>
      </Drawer>
      
      {/* Contenido principal - con margen para el AppBar */}
      <Box
        component="main"
        sx={{
          flexGrow: 1,
          p: 3,
          width: `calc(100% - ${drawerOpen ? 240 : 56}px)`,
          marginLeft: `${drawerOpen ? 240 : 56}px`,
          marginTop: '64px',
          minHeight: 'calc(100vh - 64px)',
          backgroundColor: '#f5f5f5'
        }}
      >
        {/* Aquí se inyectará el contenido de cada página */}
      </Box>
    </>
  );
}